<template>
  <div id="extra_style" class="extra_color">
    <div class = "check-top"></div>
      <div class = "checkbox_radius">
      <el-form ref="form" size="mini" class="extra_background">
          <el-checkbox v-model="extraInfo.debitCardServer" label="" class = "label_apply" true-label="0" false-label="" disabled> </el-checkbox>
      <img src="../../assets/question.png" style="height: 16px;" class="img_center" @click="_popuptext('1')"></img>

      </el-form>
      <el-form ref="form" size="mini" class="extra_background">
          <el-checkbox v-model="extraInfo.enjoyCardServer" label="(12//)" class = "label_apply" true-label="1" false-label="0" > </el-checkbox>
      <img src="../../assets/question.png" style="height: 16px;" class="img_center" @click="_popuptext('2')" ></img>

      </el-form>
      <el-form ref="form" size="mini" class="extra_background">
          <el-checkbox v-model="extraInfo.creditServer" label="(12//)" class = "label_apply" true-label="1" false-label="0"> </el-checkbox>
      <img src="../../assets/question.png" style="height: 16px;" class="img_center" @click="_popuptext('3')"></img>
      </el-form>
      <el-form ref="form" size="mini" class="extra_background">
          <el-checkbox v-model="extraInfo.autoBuyerServer" label="" class = "label_apply" true-label="1" false-label="0"> </el-checkbox>
      <img src="../../assets/question.png" style="height: 16px;" class="img_center" @click="_popuptext('4')"></img>
      </el-form>
      <el-form ref="form" size="mini" class="extra_background">
          <el-checkbox v-model="extraInfo.bigExchangeServer" label="()" class = "label_apply" true-label="1" false-label="0"> </el-checkbox>
      <img src="../../assets/question.png" style="height: 16px;" class="img_center" @click="_popuptext('5')"></img>
      </el-form>
      <el-form ref="form" size="mini" class="extra_background">
          <el-checkbox v-model="extraInfo.billSmsServer" label="()" class = "label_apply" true-label="1" false-label="0"> </el-checkbox>
      <img src="../../assets/question.png" style="height: 16px;" class="img_center" @click="_popuptext('6')"></img>
      </el-form>
      <el-form ref="form" size="mini" class="extra_background">
          <el-checkbox v-model="extraInfo.openPhoneBankServer" label="()" class = "label_apply" true-label="1" false-label="0"> </el-checkbox>
      <img src="../../assets/question.png" style="height: 16px;" class="img_center" @click="_popuptext('7')"></img>
      </el-form>
      <div class="extra_background">
      <el-form ref="form" size="mini" class="special_background">
          <el-checkbox v-model="apply_emv_checked" label="EMV" class = "label_apply" > </el-checkbox>
      <img src="../../assets/question.png" style="height: 16px;" class="img_center" @click="_popuptext('8')"></img>
      </el-form>
      <el-form ref="form" size="mini" class="special_background">
          <el-checkbox v-model="extraInfo.emv" label="VISA" class = "label_card" true-label="1" :disabled ="check_visa"> </el-checkbox>
          <el-checkbox v-model="extraInfo.emv" label="MASTERCARD" class = "label_card" true-label="0" :disabled ="check_mastercard"> </el-checkbox>
          <!-- <el-radio v-model="extraInfo.emv" label="1" class="radio_position" :disabled="check_visa" @change="_changeRadioVisaStatus()">VISA</el-radio>
          <el-radio v-model="extraInfo.emv" label="0" :disabled="check_mastercard" >MASTERCARD </el-radio> -->
      </el-form>
      </div>

      <el-form ref="form" size="mini" class="extras_background">
          <el-checkbox v-model="extraInfo.autoPayServer" label="" class = "label_repay" true-label="1" false-label="0" @change="_showToggle"> </el-checkbox>
      <img src="../../assets/question.png" style="height: 16px;" class="img_center" @click="_popuptext('9')"></img>
      </el-form>
      <div class="repay_background" v-show="autoPay">
      <div class="special_background" >
          <div class="select_method_cardno"   >:<input @blur="_testCardNo()"   class = "bank_no" maxLength="19" v-model="extraInfo.cardNo"></input>     </div>

      <div style="margin-top:5px;">
          <font class="select_method" >: </font>
        <el-select v-model="extraInfo.payMethod" placeholder="">
          <el-option
              v-for="item in pay_method"
              :key="item.value"
              :label="item.label"
              :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class = "repayment_method">
          <font>: </font>
          <el-radio v-model="extraInfo.payAmt" label="1" class="radio_position"></el-radio>
          <el-radio v-model="extraInfo.payAmt" label="2"></el-radio>
      </div>
      </div>
      </div>
      <div class="font_extra">
      <font color="#ff8787" class="extra_service"></font>
      </div>
      <!-- <div class = "button_background">
        <mt-button @click="_sendExtraInfo" type="default"  class="btn_extra" ></mt-button>
      </div> -->
      <div class="extra-next-btn">
        <button v-on:click="_sendExtraInfo">
            
        </button>
      </div>
      </div>
    <mt-popup
      v-model="popupVisible"
      position = "left"
      popup-transition = "popup-fade"
    >
    <template>
      {{popupText}}
    </template>
    </mt-popup>
  </div>


</template>

<script>

  import _ui from '@/base/BaseUi'
  import { MessageBox, Toast,Popup } from 'mint-ui';
  export default {
    name: "mail",
    data() {
      return {
         pay_method:[{
                value:'0',
                label:''
         },
                  {
                value:'1',
                label:''
                  }

         ],
        popupVisible:false,
        card_org_code:'',
        card_flag:'',
        card_prod_code:'',
        card_card_type:'',
        apply_emv_checked:'',
        autoPay:false,
        is_mastercard:false,
        is_visa:false,
        check_visa:false,
        check_mastercard:false,
        popupText:'',
        extraInfo:{
        pageIndex : '5',
        orderId : this.$store.state.allUserData.orderId,
        debitCardServer:"",
        emv:"",
        enjoyCardServer:"",
        creditServer:"",
        autoBuyerServer:"",
        bigExchangeServer:"",
        billSmsServer:"",
        openPhoneBankServer:"",
        autoPayServer:"",
        cardNo:"",
        payAmt:"",
        payMethod:"",
        token:this.$store.state.toKen,
        }
      }
    },

    mounted: function () {
      let vm = this;
      let emv_param= {
        pageIndex:'1',
        orderId:this.$store.state.allUserData.orderId,
      }
      let extra_param = {
        pageIndex: '5',
        orderId: this.$store.state.allUserData.orderId,
      };
      vm.$axios.post(this.$Rest.api._information, extra_param)
      .then(function(resp) {
        if(resp.data.rtnCode == '200') {
          _ui.hideLoading();
          console.log(resp);
          if(resp.data.records) {
            vm.extraInfo = resp.data.records[0];
          }

          if(vm.extraInfo.emv !== null && vm.extraInfo.emv !== ""){
            vm.apply_emv_checked = true;
          }
          if(vm.extraInfo.autoPayServer === "0"){
              vm.autoPay = false;
          }else if(vm.extraInfo.autoPayServer === "1"){
              vm.autoPay = true;
          }
        } else {
          vm.$store.commit('changeShow', 'error');
        }
      })
      .catch(function(err) {
        _ui.hideLoading();
        console.log(err);
        // _ui.showToast(err);
      });
      _ui.showLoading("");
      vm.$Rest._post(this.$Rest.api._information, emv_param)
        .then(function (resp) {
          _ui.hideLoading();
        //  console.log(resp);
        if(resp.data.records) {
         vm.card_org_code = resp.data.records[0].cardOrgCode;
        //  vm.card_org_code = "C,M";
        //  console.log(vm.card_org_code);
         vm.card_flag = resp.data.records[0].flag;
         vm.card_prod_code = resp.data.records[0].prodCd;
         vm.card_card_type = resp.data.records[0].cardType;
        }
      let card_param = {
          D_cardTypeCode:vm.card_card_type,
          D_cardProdCode:vm.card_prod_code,
          // D_goldRegularCode:vm.card_flag
          D_goldRegularCode:vm.card_flag
      };
      console.log(card_param);
      vm.$Rest._post(vm.$Rest.api._getCard, card_param)
        .then(function (resp) {
          _ui.hideLoading();
          console.log(resp);
          vm.is_mastercard = resp.data.message[0].D_isMasterEMV;
          vm.is_visa = resp.data.message[0].D_isVisaEMV;
          // vm.is_mastercard = "0";
          // vm.is_visa = "1";
          //  console.log(vm.is_visa);
          //  console.log(vm.is_mastercard);
          vm._checkSplitStr();
        })
        .catch(function (err) {
          _ui.hideLoading();
          console.log(err);
          // _ui.showToast(err);
        });
        })
        .catch(function (err) {
          _ui.hideLoading();
          console.log(err);
          // _ui.showToast(err);
        });

    },
    methods: {

      _popuptext:function(number){
        let vm = this;
        switch (number) {
                case "1":
                  MessageBox({
                    title: '',
                    message: '',
                    confirmButtonText: ''
                  });
                  // vm.popupVisible = true;
                  // vm.popupText = ""
                  break;
                case "2":
                  MessageBox({
                    title: '',
                    message: '\"\" 72 ()72 \"\"',
                    confirmButtonText: ''
                  });
                  // vm.popupVisible = true;
                  // vm.popupText = "\"\" 72 ()72 \"\""
                  break;
                case "3":
                  MessageBox({
                    title: '',
                    message: '\"\"+T+1T-3T+1T+1',
                    confirmButtonText: ''
                  });
                  // vm.popupVisible = true;
                  // vm.popupText = "\"\"+T+1T-3T+1T+1"
                  break;
                case "4":
                  MessageBox({
                    title: '',
                    message: '',
                    confirmButtonText: ''
                  });
                  // vm.popupVisible = true;
                  // vm.popupText = ""
                  break;
                case "5":
                  MessageBox({
                    title: '',
                    message: '\"\" 1500 100 \"\"  IC ',
                    confirmButtonText: ''
                  });
                  // vm.popupVisible = true;
                  // vm.popupText = "\"\" 1500 100 \"\"  IC ";
                  break;
                case "6":
                  MessageBox({
                    title: '',
                    message: '\"\" \"\"\"\"',
                    confirmButtonText: ''
                  });
                  // vm.popupVisible = true;
                  // vm.popupText = "\"\" \"\"\"\"";
                  break;
                case "7":
                  MessageBox({
                    title: '',
                    message: '24',
                    confirmButtonText: ''
                  });
                  // vm.popupVisible = true;
                  // vm.popupText = "24";
                  break;
                case "8":
                  MessageBox({
                    title: '',
                    message: 'EMV ',
                    confirmButtonText: ''
                  });
                  // vm.popupVisible = true;
                  // vm.popupText = "EMV ";
                  break;
                case "9":
                  MessageBox({
                    title: '',
                    message: ' ',
                    confirmButtonText: ''
                  });
                  // vm.popupVisible = true;
                  // vm.popupText = " ";
                  break;
                default:
                  break
        }

      },

      _testCardNo:function(){
        let re = /^[0-9]{1,19}$/
        let vm = this;
        if(vm.extraInfo.autoPayServer ==='1'){
          if(vm.extraInfo.cardNo.length == 0 ){
            Toast({
              message: '',
              duration: 1000
            })
          }else if(!re.test(vm.extraInfo.cardNo) ){
          Toast({
            message: '',
            duration: 1000
          })
          return false
        }else{
          return true
        }
        }
      },

      _checkSplitStr:function(){
        let vm = this;
        let str = vm.card_org_code;
        var strs = new Array();
        strs = str.split(",");
        for(let i = 0;i<strs.length;i++){
          console.log(strs[i]);
          if(strs[i].indexOf("M")!=-1 && vm.is_mastercard==='1'){
            vm.check_mastercard = false;
            console.log(vm.check_mastercard);
          }else{
            vm.check_mastercard = true;
            console.log(vm.check_mastercard);
          }
          if(strs[i].indexOf("V")!=-1 && vm.is_visa === '1'){
            vm.check_visa = false;
          }else{
            vm.check_visa = true;
          }
        }
      },

      _changeRadioVisaStatus:function(){
        let vm = this;
        if(vm.apply_emv_checked === true){
          if(vm.check_visa === '1'){
            vm.check_visa = '0';
          }else if (vm.check_visa === '0'){
            vm.check_visa = '1';
          }
        }else{
          vm.check_visa = '0';
          MessageBox('',"EMV");
        }
      },

      _showToggle:function(){
        let vm = this;
        if(vm.extraInfo.autoPayServer === "0"){
            vm.autoPay = false;
        }else if(vm.extraInfo.autoPayServer === "1"){
            vm.autoPay = true;
        }
      },

      loadTop: function () {
        this.$refs.loadmore.onTopLoaded();
      },
      _sendExtraInfo: function(){
      let vm = this;
      vm.extraInfo.pageIndex = '5';
      vm.extraInfo.token = this.$store.state.toKen;
      if(vm.extraInfo.autoPayServer === '1'){
        if (vm.$tools._isNullOrEmpty(vm.extraInfo.cardNo)) {
          _ui.showToast("");
          return;
        }
        if (vm.$tools._isNullOrEmpty(vm.extraInfo.payMethod)) {
          _ui.showToast("");
          return;
        }
        if (vm.$tools._isNullOrEmpty(vm.extraInfo.payAmt)) {
          _ui.showToast("");
          return;
        }
      }
      _ui.showLoading("");
      vm.$Rest._post(this.$Rest.api._extra, vm.extraInfo)
        .then(function (resp) {
          _ui.hideLoading();
          vm.$router.replace('picture');
        })
        .catch(function (err) {
          _ui.hideLoading();
          _ui.showToast(err);
        });
      },
    }
  }
</script>

<style >

      .checkbox_radius{
        background-color: #eeeeee;
      }

      .extra_color{
        background-color:#f6f5fa;
      }

      .single_select .el-input__inner{
          margin-left:0px;
          width:90px;
      }

      .single_select .el-input{
          width:90px;
      }

      .extra_color .el-select{
          margin-left: 0.3rem;
      }

      .el-checkbox+.el-checkbox {
        margin-left: 10px;
      }

    .extra_color .label_apply{
      font-size: 15px;
      margin-left:30px;
      margin-top:12px;
      color:#7c99d6;
    }

    .extra_color .label_repay{
      font-size: 12px;
      margin-left:30px;
      margin-top:12px;
      color:#7c99d6;
    }

    .extra_color .label_card{
      font-size: 12px;
      margin-left:70px;
      color:#919191;
    }

    .extra_input .el-input__inner{
      width:100%;
      height:25px;
      margin-left:20px;
      margin-top:5px;
    }

    .extra_input .el-input{
      width:50%;
      height:25px;
      margin-left:5px;
    }

    .extra_method .el-input__inner{
      width:100%;
      height:25px;
      margin-left:20px;
    }

    .extra_method .el-input{
      width:50%;
      height:25px;
      margin-left:45px;
    }


    .extra_service{
      width:50px;
      font-size: 12px;
    }

    .font_extra{
      width:80%;
      margin-top:20px;
      margin-left:30px;
    }

    .extra_color .bank_no{
      font-size:12px;
      border:1px solid #919191;
    }

  .select_method{
       font-size: 12px;
       margin-left:40px;
       margin-top:10px;
       color:#919191;
       width:250px;
  }

  .select_method_cardno{
       font-size: 12px;
       margin-left:40px;
       margin-top:10px;
       color:#919191;
       width:250px;
  }

  .extra_color .repayment_method{
       font-size: 12px;
       margin-left:40px;
       margin-top:5px;
       color:#919191;
       width:80px;
       display:inline;
  }

  .radio_position{
      margin-left:0.5rem;
      margin-top:10px;
  }

  .extra_background{
    background-color:white;
    margin:10px;
    height:45px;
    margin-top: -3px;
  }
  .repay_background{
    background-color:white;
    margin:10px;
    height:100px;
    margin-top: -10px;
  }
  .extras_background{
    background-color:white;
    margin:10px;
    height:40px;
    margin-top:30px;
  }

  .special_background{
    background-color:white;
    height:35px;
  }

  .extra_color .el-input .el-input--suffix{
    width:120px;
  }

  .extra_color .el-input__inner {
      -webkit-appearance: none;
      background-color: #fff;
      background-image: none;
      border-radius: 4px;
      border: 0px solid #dcdfe6;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      color: #606266;
      display: inline-block;
      font-size: inherit;
      height: 20px;
      line-height: 40px;
      outline: 0;
      padding: 0 15px;
      -webkit-transition: border-color .2s cubic-bezier(.645,.045,.355,1);
      transition: border-color .2s cubic-bezier(.645,.045,.355,1);
      width: 3.0rem;
  }
  .extra_color .el-input__inner::placeholder {
      color: #919191;
      font-size:12px;
  }

  .extra_color .el-input .el-input--suffix{
    width:120px;
  }

  .el-select .el-input .el-select__caret {
    margin-top: -5px;
  }

  .extra_color .el-select .el-input .el-select__caret.is-reverse {
      -webkit-transform: rotateZ();
      transform: rotateZ(180deg);
  }

  .extra_color .check-top {
      padding-top: 7px;
      background: #f6f5fa;
    }

  .button_background{
    height: 140px;
  }

  .btn_extra{
      font-size: 15px;
      border-radius: 30px;
      background: #267cf7;
      color: white;
      margin-top: 10%;
      margin-left:60px;
      width: 5rem;
      height: .7rem;
  }

  .extra-next-btn button {
    width: 5rem;
    height: .8rem;
    border: 2px solid #fff;
    color: #fff;
    background: #3174f6;
    margin: 0 auto;
    border-radius: 40px;
    outline: none;
  }

  .extra-next-btn{
    text-align: center;
    margin-top: .5rem;
    padding-bottom: .2rem;
    height: 1.5rem;
  }

</style>



