        <P>
            30
        </P>
        <P>
            3653090
        </P>
        <P>
            
        </P>
        <P>
            31183
        </P>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
      return {

      }
  },
  computed: {
    isHide() {
        return this.$store.state.showRevenue
    }
  },
  methods: {
        hideRevenue () {
            this.$store.commit('SHOW_REVENUE', true)
        }
  }
}
</script>

<style scoped lang="scss">
    .hide-all {
        display: none;
    }
    .credit-style {
        width: 7.5rem;
        height: 13.34rem;
        padding: .3rem;
        background: #fff;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 998;
        .close {
            font-size: .4rem;
        }
        h2 {
           font-size: .4rem;
           text-align: center;
           line-height: .8rem;
           font-weight: 700;
        }
        p {
            text-indent: .6rem;
            font-size: .3rem;
            line-height: .5rem;
        }
    }
</style>




